<template>
  <div>
    <van-nav-bar title="签到" left-arrow @click-left="$toPage(-1)" >
      <van-icon name="arrow-left" size="24px" color="#fff" slot="left" />
    </van-nav-bar>

    <div class="head">
      <div class="circle" @click="sign">
        <p class="p2">签到</p>
        <hr />
        <p class="p3">积分 +{{ signgetJF }}</p>
      </div>
      <span class="left">
        <p class="p5">当前积分</p>
        <p class="p4">{{ countInfo[1] }}</p>
      </span>
      <div class="head-foot">
        <p class="p1">坚持每天签到可以获得多重奖励哦~</p>
      </div>
      <span class="right">
        <p class="p5">累计签到{{ signSum }}天</p>
        <button class="btn" @click="$toPage('exchange')">积分兑换</button>
      </span>
    </div>

    <div class="foot">
      <div class="date">{{ signnowdate }}</div>
      <div>
        <div class="calendar">
          <table
            class="sign_tab"
            border="0px"
            cellpadding="0px"
            cellspacing="0px"
          >
            <thead>
              <tr>
                <th>日</th>
                <th>一</th>
                <th>二</th>
                <th>三</th>
                <th>四</th>
                <th>五</th>
                <th>六</th>
              </tr>
            </thead>
            <tbody>
              <tr
                v-for="(item, index) in dateArr"
                :key="index"
                v-if="contains(item)"
              >
                <td
                  v-for="data in item"
                  :class="{ 'ui-state-default': true }"
                  v-if="isCheck(data)"
                ></td>
                <td v-else>
                  {{ data }}
                </td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    </div>

    <!--签到记录-->
    <div class="look" @click="click()">
      <div class="look1"></div>
      <div class="look2">积分来源</div>
    </div>

    <van-actionsheet v-model="show" title="签到记录">
      <ul class="signMsg">
        <li>
          <span>签到日期</span>
          <span>签到积分</span>
        </li>
        <li v-for="item in signHisList">
          <span>{{ item.createdate }} {{ item.createtime }}</span>
          <span>本次签到获得{{ item.integral }}积分</span>
        </li>
        
      </ul>
    </van-actionsheet>
  </div>
</template>
<script>
export default {
  data() {
    return {
      show: false,
      countInfo: [],
      signSum: 0,
      signnowdate: "",
      signSumInMouth: 0,
      signHisList: [],
      signList: [],
      today: new Date(),
      year: "",
      month: "",
      day: "",
      date: "",
      dateArr: [],
      checkin: [],
      signgetJF: 0
    };
  },
  created() {
    this.year = this.today.getFullYear();
    this.month = this.today.getMonth() + 1;
    this.day = this.today.getDay();
    this.date = this.today.getDate();

    this.getCalendar();
  },
  components: {},
  computed: {},
  //页面打开时调用
  mounted() {
    this.getJF();
    this.getHisSign();
    this.getSignMsg();
  },
  methods: {
    click() {
      //this.show = true;
      this.$toPage('integralRecord');
    },
    onSelect(item) {
      // 点击选项时默认不会关闭菜单，可以手动关闭
      this.show = false;
    },
    getSignMsg() {
      JNB.query({
        serverName:
          "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "IntegralModule",
        method: "queryIntegralDetailBySign",
        params: {},
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.signSumInMouth = result.data.signSum; //连续签到次数
            this.signnowdate = result.data.nowDate; //当前时间
            this.checkin = result.data.signList; //本月签到显示
            this.signHisList = result.data.signMsg; //签到历史记录
            this.rownum = result.data.signList.length;
          } else {
            this.$toast.fail("未获取签到记录！");
          }
        }
      });
    },

    getJF() {
      JNB.query({
        serverName:
          "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "OrderInfoModule",
        method: "appCountCompInfo",
        params: {
          compid: this.$loadUserInfo().comp.storeId
        },
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            this.countInfo = result.data;
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    sign() {
      JNB.query({
        serverName:
          "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "IntegralModule",
        method: "signIn",
        params: {},
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            // 更新用户登录状态
            //this.countInfo = result.data;
            this.$toast("签到成功！");
            this.getJF();
            this.getHisSign();
            this.getSignMsg();
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    getHisSign() {
      JNB.query({
        serverName:
          "orderServer" + this.$ser_in(this.$loadUserInfo().comp.storeId),
        cls: "IntegralModule",
        method: "getHisSignIn",
        params: {},
        callback: data => {
          let result = JSON.parse(data);
          if (result.code === 200) {
            this.signSum = result.data.times;
            this.signgetJF = result.data.signGetJf;
          } else {
            this.$toast.fail(result.message);
          }
        }
      });
    },
    isLeap() {
      const year = this.year;
      if (year % 4 == 0 && year % 100 > 0) {
        return true;
      } else if (year % 400 == 0 && year % 3200 > 0) {
        return true;
      } else {
        return false;
      }
    },
    getLen() {
      const month = this.month;
      if (month == 2) {
        if (this.isLeap) {
          return 29;
        } else {
          return 28;
        }
      } else {
        if (month < 8) {
          if (month % 2 > 0) {
            return 31;
          } else {
            return 30;
          }
        } else {
          if (month % 2 > 0) {
            return 30;
          } else {
            return 31;
          }
        }
      }
    },
    getCalendarTime() {
      return this.year + "-" + this.month + "-" + this.date;
    },
    getCalendar() {
      var len = this.getLen();
      var d = new Date(this.year, this.month - 1, 1);
      var dfw = d.getDay();
      var arr = new Array();
      var tem = 0;
      for (var i = 0; i < 6; i++) {
        arr[i] = new Array();
        for (var j = 0; j < 7; j++) {
          tem++;
          if (tem - dfw > 0 && tem - dfw <= len) {
            arr[i][j] = tem - dfw;
          } else {
            arr[i][j] = "";
          }
        }
      }
      this.dateArr = arr;
    },
    nextMonth() {
      if (this.month == 12) {
        this.year++;
        this.month = 1;
      } else {
        this.month++;
      }
      this.getCalendar();
    },
    prevMonth() {
      if (this.month == 1) {
        this.year--;
        this.month = 12;
      } else {
        this.month--;
      }
      this.getCalendar();
    },
    contains(arr) {
      if (
        arr[0] == "" &&
        arr[1] == "" &&
        arr[2] == "" &&
        arr[3] == "" &&
        arr[4] == "" &&
        arr[5] == "" &&
        arr[6] == ""
      ) {
        return false;
      } else {
        return true;
      }
    },
    isCheck(index) {
      for (let i in this.checkin) {
        if (index == this.checkin[i]) {
          return true;
        }
      }
      return false;
    }
  }
};
</script>
<style scoped>
