}
.p5 {
  font-size: 26px;
  font-family: PingFang-SC-Regular;
  color: #ffffff;
}
.look1 {
  height: 100px;
  margin-left: 290px;
  float: left;
  width: 50px;
  background-color: #5bd999;
  background: url("../../assets/me_icon/rule2x.png") no-repeat center;
